.honored {

    margin-top: 250px;

.flex {

display: flex;
justify-content: space-between;
margin-bottom: 50px;

@media screen and (max-width:1080px) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
}

    .left {
       
        h2 {
            font-family: "tusker";
            line-height: 1em;
            font-size: 110px;
            font-weight: 100;
            padding: 0;
            margin: 0;
            color: #fbf3e9;
        }
    }
    .right {

        margin-right: 50px;
    width: 55%;

   
        .title {
            display: flex;
            justify-content: space-between;
            font-family: "migra";
            font-size: 25px;
            border-bottom: 1px solid white;
            padding-bottom: 20px;
            .flex-grow {
                width: 33.33%;
            }
        }
        .line {
            display: flex;
            justify-content: space-between;
            font-family: "migra";
            font-size: 13px;
            border-bottom: 1px solid white;
            padding: 20px  0;
            .flex-grow {
                width: 33.33%;

            }
        }
    }
}
.contact-me {
    margin-right: 50px;
}
.bandeau {
    margin: 200px 0;
    .name {
        margin-left: 100px;
    }
}
}