.myself {
    margin-top: 450px;
    max-width: 90%;
    margin-left: 10%;
    @media screen and ( max-width:1080px) {
      margin-top: 150px;
      }
    
    .name {
        font-size: 16px;
        text-align: end;
    }
h2 {
    font-family: "tusker";
    font-size: 100px;
    line-height: 0.2em;
    display: block;
}


.flex {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
    @media screen and ( max-width:1080px) {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
}
p {
    font-size: 2rem;
    
}
    .description {
       font-size: 2rem;
       max-width: 40vw;
       @media screen and ( max-width:1080px) {
        max-width: 100%;
      }
    }

    video {
        z-index: 7;
        width: 100%;

    }
   
}

.corona {
    position: relative;
    max-width: 300px;
    margin-left: 10%;
    margin-top: 10%;
    p {
        line-height: 0.5em;
    }
   
    .relative {
        position: relative;
    }
    #img {
       position: absolute;
        width: 86px;
        top: -4px;
        right: -10px;
        
    }
    #fleche {
        position: absolute;
        width: 120px;
        top: -30px;
        right: -70px;
    }
}