.nav-container {
    position: sticky;
    top: 0;
    width: 100vw;
    z-index: 3;

}

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 25px 0;
    border-bottom: 1px solid rgb(255, 255, 255);
    margin: 0 25px;
    
    .right ul {
        display: flex;
    justify-content: center;
    align-items: center;

    li {
        padding-left: 10px;
    }
    }
    .left {
        display: flex;
    justify-content: center;
    align-items: center;
    p{
        padding-right: 20px;

        a {
            font-family: "migra";
        }
    }
    }

    .relative-strike {
        position: relative;
    }
    .strike {
        position: absolute;
        width: 100%;
        height: 2px;
        background-color: white;
        top: 50%;
    }
}