.connect {
    border-bottom: 1px solid white;
    padding-bottom: 20px;
    h2 {
        font-family: "tusker";
        line-height: 1em;
        font-size: 218px;
        font-weight: 100;
        padding: 0;
        margin: 0;
        color: #fbf3e9;
    }
    p {
        font-size: 28px;
        line-height: 40px;
    }
    .button-container {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 100px;
        

        .contact-me {
            margin: 8px 0;
            margin-right: 6px;
            color: #fbf3e9;

        }
    }
    .flex {
        display: flex;
        justify-content: flex-start;
        align-items: center;

        .contact-me-inverse{
            margin-left: 6px;
            color: #fbf3e9;
            padding: 10px;

        }
    }
  
}